.carousel{
  overflow: hidden;
  cursor: grab;
  width: 30%;
  height: 50%;
  margin-left: 440px;
  
}
.inner{
  display: flex;
}
.item{
  min-height: 25rem;
  min-width: 20rem;
  padding: 40px 20px;
}
.item img{
  width: 100%;
  height: 100%;
  border-radius: 2rem;
  pointer-events: none;
}